<!-- @format -->

<template>
  <div class="movie-detail">
    <div class="movie-header">
      <div class="movie-title">
        <h2>{{ movie.Title }}</h2>
        <p>{{ movie.Year }}</p>
      </div>
    </div>
    <div class="movie">
      <img :src="movie.Poster" alt="Movie Poster" />
      <div class="info">
        <p>{{ movie.Plot }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onBeforeMount } from "vue";
import { useRoute } from "vue-router";
import key from "@/key.js";
export default {
  name: "movieDetail",
  setup() {
    const movie = ref({});
    const route = useRoute();

    onBeforeMount(() => {
      fetch(
        `https://www.omdbapi.com/?apikey=${key.apikey}&i=${route.params.id}&plot=full`
      )
        .then((response) => response.json())
        .then((data) => {
          movie.value = data;
        });
    });
    return {
      movie,
    };
  },
};
</script>
<style lang="scss" scoped>
body {
  background-color: #026c6f;
  .movie-detail {
    width: 100%;
    height: calc(100vh - 68px);
    .movie-header {
      display: flex;
      justify-content: space-around;
      gap: 20px;
      padding: 15px;
      flex-direction: column;
      .movie-title {
        color: white;
        h2 {
          margin-bottom: 10px;
        }
      }
    }
    .movie {
      display: flex;
      justify-content: space-around;
      align-items: center;
      gap: 30px;
      padding: 16px 20px;
      img {
        width: 300px;
        height: 300px;
      }

      .info {
        height: auto;
        display: flex;
        align-items: end;
        p {
          color: white;
          line-height: 1.6;
        }
      }
    }
  }
  @media (max-width: 768px) {
    .movie {
      flex-direction: column;
      padding: 16px 0;
    }
  }
}
</style>
