<!-- @format -->

<template>
  <div class="movie-detail">
    <main>
      <div class="form">
        <form @submit.prevent="searchMovies()">
          <input
            type="text"
            placeholder="What are you loking for?"
            v-model="search"
          />
          <input type="submit" value="Search" />
        </form>
      </div>

      <div class="container">
        <!-- <div class="box" v-for="movie in movies" :key="movie">
          <router-link to="/movie/4949omar">
            <div class="img"></div>
          </router-link>
        </div> -->
        <div class="box" v-for="movie in movies" :key="movie.imdbID">
          <router-link :to="'/movie/' + movie.imdbID">
            <div class="img">
              <img :src="movie.Poster" alt="" />
              <p>{{ movie.Type }}</p>
            </div>
            <div class="detail">
              <p>{{ movie.Year }}</p>
              <h3>{{ movie.Title }}</h3>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { ref } from "vue";
import key from "@/key.js";
export default {
  name: "HomeView",
  setup() {
    const search = ref("");
    const movies = ref([]);
    const searchMovies = () => {
      if (search.value != "") {
        fetch(`https://www.omdbapi.com/?apikey=${key.apikey}&s=${search.value}`)
          .then((response) => response.json())
          .then((data) => {
            movies.value = data.Search;
            search.value = "";
          });
      }
    };

    return {
      search,
      movies,
      searchMovies,
    };
  },
};
</script>
